import Vue from 'vue';
import Router from 'vue-router';

import permissionRouter from './permission';
import carRouter from './car';
Vue.use(Router);

export default new Router({
    routes: [{
        path: '/',
        component: () =>
                import('../pages/Welcome')
    }, {
        path: '/car-dealer',
        component: () =>
                import('../pages/CarDealer')
    },
    {
        path: '/rent',
        component: () =>
                import('../pages/RentCar')
    },
    {
        path: '/finance',
        component: () =>
                import('../pages/Finance')
    },
    {
        path: '/insurance',
        component: () =>
                import('../pages/Insurance')
    },
    permissionRouter,
    carRouter
    ]
});



